@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

a {
  text-decoration: none;
  color: grey;
}
.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:80px;right:0;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin-bottom:50px}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.header-bar{display:flex;justify-content:space-between;margin:0;padding:1%;border-bottom:2px solid grey}.header-bar .bar-title{margin-left:2%;width:25%}.header-bar .bar-title h1{font-size:2rem;font-family:'Amatic SC', cursive;margin:0;color:lightpink}.header-bar .bar-title p{font-size:0.8rem;margin:0}.header-bar .bar-options{width:75%;display:flex;justify-content:space-evenly}.header-bar .bar-options .bar-nav-links{width:50%;display:flex;justify-content:space-evenly;align-items:center}.header-bar .bar-options .bar-nav-links .bar-option{text-decoration:none;color:black;font-weight:900;font-size:1.2rem;font-family:'Playfair Display', serif}.header-bar .bar-options .bar-nav-links .bar-option::after{color:lightpink;display:block;content:'';transform:scaleX(0);transition:transform 0.5s ease-in-out;border-bottom:2px solid;transform-origin:0% 50%}.header-bar .bar-options .bar-nav-links .bar-option:hover::after{transform:scaleX(1)}.header-bar .bar-search-field{width:25%;display:flex;justify-content:flex-end;align-items:center}.header-bar .bar-search-field .bar-cart-link{width:100%;border:2px solid;border-radius:8px;padding:10%;display:flex;justify-content:space-evenly;align-items:center}.header-bar .bar-search-field .bar-cart-link:hover{cursor:pointer}

.social-bar{display:flex;justify-content:space-between;padding:1%}.social-bar .facebook{color:blue}.social-bar .instagram{background:linear-gradient(45deg, #405DE6, #5851DB, #833AB4, #C13584, #E1306C, #FD1D1D);border-radius:10%}.social-bar .pinterest{color:red}

.footer-styles{border-top:2px solid grey}.footer-styles .logo-container{max-width:100%;margin-top:8px}.footer-styles .logo-container .logo{width:100%;height:50px}.footer-styles .contact{width:10%;margin-left:auto;display:flex;flex-direction:column;margin-right:5%}.footer-styles .contact p{margin:0;margin-bottom:8px;font-size:24px;font-family:'Amatic SC', cursive}.footer-styles #copyright{text-align:left;margin-left:2%}

.product-card{margin-bottom:25px;width:22%;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.product-card .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.product-card .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.product-card:hover .image{opacity:0.8}.product-card:hover .custom-button{opacity:0.85;display:flex}.product-card .product-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.product-card .product-footer .name{width:90%;text-align:left;margin-bottom:15px}.product-card .product-footer .price{width:10%}

.home-page{padding-top:80px;padding-bottom:128px}.home-page .welcome-section{display:flex;justify-content:space-between}.home-page .welcome-section .welcome-content{width:45%;margin:0 auto;margin-bottom:64px}.home-page .welcome-section .welcome-content h1{margin:0;margin-bottom:25px;font-size:3.5rem;font-family:'Amatic SC', cursive}.home-page .welcome-section .welcome-content p{margin:0 auto}.home-page .featured-product-wrapper h2{font-family:'Amatic SC', cursive;margin:0;margin-left:2%;margin-bottom:25px;font-size:2rem;text-align:left}.home-page .featured-product-wrapper .featured-products{display:flex;justify-content:space-around}

.about-page{background-color:lightpink;padding-top:80px;padding-bottom:164px}.about-page .profile-content{display:flex;align-items:center;width:40vw;margin:0 auto}.about-page .profile-content .profile-text h2{font-family:'Amatic SC';margin:0;margin-bottom:2%;font-size:3rem}.about-page .profile-content .profile-text p{width:75%;margin:0 auto;line-height:32px}.about-page .profile-content #profile{border-radius:50%;width:50%}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center;justify-content:space-around}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.checkout-page{background-color:white;width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{height:150px;text-align:center;margin-top:30px;margin-left:auto;margin-bottom:50px;font-size:2.5rem;display:flex;flex-direction:column;justify-content:space-around}.checkout-page .test-warning{text-align:center;margin-top:40px;font-size:24px;color:red}

.shop-page{padding-top:80px;padding-bottom:128px}.shop-page h2{text-align:left;margin:0;margin-left:32px;margin-bottom:25px;font-size:3rem;font-family:'Amatic SC', 'sans serif'}.shop-page .shop-content{display:flex;flex-wrap:wrap;justify-content:space-evenly;width:100vw}

.notfound{margin-top:80px;margin-bottom:80px}.notfound h1{color:lightpink;text-align:center}.notfound button{font-weight:900;margin:0 auto;font-size:1rem;color:black;cursor:pointer;padding:2%}

